/* ============================================
   GLOBAL STYLE
=============================================== */
.match-container {
  padding: 20px;
  min-height: 100vh;
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364);
  font-family: 'Prompt', sans-serif;
  color: rgb(14, 13, 13);
}

/* ============================================
   HEADER
=============================================== */
.match-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(247, 246, 246, 0.08);
  padding: 14px 20px;
  border-radius: 12px;
  border-left: 6px solid #00ff9d;
  box-shadow: 0 4px 10px rgba(0,0,0,0.25);
  margin-bottom: 20px;
  font-size: 18px;
}

.btn-back {
  padding: 10px 14px;
  background: #ff5c5c;
  border: none;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  transition: 0.2s;
}

.btn-back:hover {
  background: #ff4242;
}

/* ============================================
   PAGE GRID
=============================================== */
.match-grid {
  display: flex;
  gap: 22px;
  flex-wrap: wrap;
}

.left, .right {
  flex: 1;
  min-width: 350px;
}

/* ============================================
   PLAYER LIST (LEFT)
=============================================== */
.player-list {
  background: rgba(255,255,255,0.05);
  padding: 16px;
  border-radius: 14px;
  border: 1px solid rgba(255,255,255,0.08);
}

.player-list h3 {
  margin-bottom: 12px;
  color: #00eaff;
  font-weight: 600;
}



.player-name {
  font-weight: bold;
  color: #00ff9d;
}

.player-rank {
  opacity: 0.8;
}

/* ============================================
   MATCH PANEL (MANUAL)
=============================================== */
.match-panel {
  background: rgba(255,255,255,0.07);
  padding: 18px;
  border-radius: 14px;
  border: 1px solid rgba(255,255,255,0.12);
  box-shadow: 0 4px 10px rgba(0,0,0,0.25);
  margin-bottom: 25px;
}

.match-panel h3 {
  color: #00ff9d;
  margin-bottom: 15px;
  font-weight: 600;
}

.team-box h4 {
  margin-bottom: 8px;
  color: #00eaff;
}

.match-panel select {
  width: 100%;
  padding: 10px;
  margin: 6px 0 14px;
  border-radius: 10px;
  border: none;
  background: #28333f;
  color: #fff;
  font-size: 15px;
}

.btn-save-match {
  background: #00ff9d;
  color: #000;
  font-weight: bold;
  border: none;
  padding: 12px;
  width: 100%;
  border-radius: 12px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s;
}

.btn-save-match:hover {
  background: #02e288;
}

/* ============================================
   MATCH LIST - NEW STYLE
=============================================== */
.match-list-panel {
  margin-top: 20px;
}

.match-title {
  font-size: 20px;
  color: #00eaff;
  margin-bottom: 12px;
}

.match-card-pro {
  background: rgba(255,255,255,0.08);
  padding: 16px;
  border-radius: 14px;
  margin-bottom: 15px;
  border-left: 6px solid #00ff9d;
  box-shadow: 0 4px 14px rgba(0,0,0,0.3);
}

.match-head {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  margin-bottom: 10px;
}

.match-number {
  color: #00ff9d;
}

.court {
  background: #0277bd;
  padding: 4px 8px;
  border-radius: 8px;
  font-size: 12px;
}

.match-team {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
}

.player {
  font-size: 15px;
  font-weight: 600;
}

.divider {
  text-align: center;
  margin: 10px 0;
  color: #bbb;
  font-weight: bold;
}

.match-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.btn-end {
  background: #2ecc71;
  padding: 8px 14px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  color: white;
}

.btn-delete {
  background: #e74c3c;
  padding: 8px 14px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  color: white;
}

/* ============================================
   RANDOM PANEL
=============================================== */


.random-panel h4 {
  color: #00ff9d;
  margin-bottom: 10px;
}

.random-panel label {
  margin-top: 10px;
}

.random-panel select {
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  border: none;
  background: #2a323a;
  color: white;
  margin-top: 6px;
}

.random-panel button {
  margin-top: 15px;
  width: 100%;
  padding: 12px;
  background: #00bcd4;
  color: #000;
  border: none;
  border-radius: 10px;
  font-weight: bold;
  cursor: pointer;
  font-size: 15px;
}

.random-panel button:hover {
  background: #009fb7;
}

/* ============================================
   Random Result
=============================================== */
.random-result {
  background: rgba(255,255,255,0.06);
  padding: 12px;
  border-radius: 10px;
  margin-top: 14px;
}

.random-result h4 {
  color: #00ff9d;
}

.random-result div {
  padding: 4px 0;
}

/* ============================================
   MOBILE
=============================================== */
@media (max-width: 768px) {
  .match-header {
    flex-direction: column;
    gap: 12px;
    font-size: 16px;
  }

  .match-card-pro {
