}

/* ‡∏õ‡∏∏‡πà‡∏° style ‡πÅ‡∏ö‡∏ö‡πÉ‡∏´‡∏°‡πà */
.action-buttons .btn {
  padding: 7px 16px;
  border-radius: 6px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}

/* ‡∏™‡∏µ‡∏Å‡∏•‡∏°‡∏Å‡∏•‡∏∑‡∏ô‡∏Å‡∏±‡∏ö gradient ‚îÄ ‡∏ó‡∏≥‡πÄ‡∏õ‡πá‡∏ô‡πÇ‡∏ó‡∏ô‡∏™‡∏î */
.btn.green {
  background: linear-gradient(135deg, #27d47c, #20b667);
}

.btn.blue {
  background: darkblue;
}

.btn.red {
  background: linear-gradient(135deg, #ff6565, #e94444);
}

.btn:hover {
  opacity: 0.92;
  transform: translateY(-2px);
}

.toggle-btn {
  background: #1976d2;
  color: white;
  padding: 10px 18px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  transition: 0.25s ease;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.toggle-btn:hover {
  background: #0d5fb8;
  transform: translateY(-2px);
}

.toggle-btn:active {
  background: #0a4a92;
  transform: translateY(0px);
}

/* ‡πÑ‡∏≠‡∏Ñ‡∏≠‡∏ô‡∏´‡∏°‡∏∏‡∏ô‡∏ï‡∏≠‡∏ô‡πÄ‡∏õ‡∏¥‡∏î */
.toggle-btn .arrow {
  display: inline-block;
  transition: transform 0.3s ease;
}

.toggle-btn.open .arrow {
  transform: rotate(180deg);
}
.match-panel {
  max-height: 0;
  overflow: hidden;
  padding: 0;
  transition: max-height 0.4s ease, padding 0.3s ease;
}

.match-panel.show {
  max-height: 2000px;   /* ‡πÉ‡∏™‡πà‡πÉ‡∏´‡∏ç‡πà‡πÜ ‡πÑ‡∏°‡πà‡∏ï‡πâ‡∏≠‡∏á‡πÄ‡∏î‡∏≤‡∏Ñ‡∏ß‡∏≤‡∏°‡∏™‡∏π‡∏á */
